
<template>
 <el-main>
    <el-form ref="form" :model="form" label-width="100px">
         <p>{{$route.query.goods_id}}</p>
        <el-form-item label="商品名称">
            <el-input v-model="form.goods_name" ></el-input>
        </el-form-item>
       
        <el-form-item label="所属分类">
            <el-select v-model="form.cate_id" clearable placeholder="请选择">
                <el-option v-for="item in options"
                :key="item.cate_id"
                :label="item.cate_name"
                :value="item.cate_id">
                </el-option>
            </el-select>
        </el-form-item>
         <el-form-item label="选择供应商">
            <el-select v-model="form.supplier_id" clearable placeholder="请选择">
                <el-option v-for="items in optionsupplier"
                :key="items.supplier_id"
                :label="items.supplier_name"
                :value="items.supplier_id">
                </el-option>
            </el-select>
        </el-form-item>
        <!--启用颜色-->
         <el-form-item label="是否启用颜色">
             <el-radio v-model="form.is_on_color" label="0">不启用</el-radio>
             <el-radio v-model="form.is_on_color" label="1">启用</el-radio>
         </el-form-item>
        <el-form-item label="启用规格颜色" v-if="form.is_on_color==1">
            <el-button type="primary" @click="addformat">添加规格</el-button>
            <table border="1" bordercolor="#999" style="border-collapse:collapse; width:80%" >
                <tbody>
                        <tr>
                            <td>颜色</td>
                            <td>原价</td>
                            <td>商城价</td>
                            <td>库存</td>
                        </tr>

                        <tr v-for="format in formats" v-bind:key="format.id">
                            <td><el-input v-model="format.color"></el-input></td>
                            <td><el-input v-model="format.smark_price"></el-input></td>
                            <td><el-input v-model="format.price"></el-input></td>
                            <td><el-input v-model="format.inventory"></el-input></td>
                        </tr>

                        
                </tbody>
            </table>
        </el-form-item>
        <!--默认规格-->
        <el-form-item label="默认规格">
            <table border="1" bordercolor="#999" style="border-collapse:collapse;">
                <tbody>
                        <tr>
                            <td>原价</td>
                            <td>商城价</td>
                            <td>库存</td>
                        </tr>
                        <tr>
                            <td><el-input v-model="form.market_price"></el-input></td>
                            <td><el-input v-model="form.goods_price"></el-input></td>
                            <td><el-input v-model="form.goods_number"></el-input></td>
                        </tr>
                </tbody>
            </table>
        </el-form-item>
        <el-form-item label="排序">
            <el-input v-model="form.goods_sort" ></el-input>
        </el-form-item>
        <el-form-item label="首页缩略图">
            <el-upload
                :action="this.url()+'/index.php/upload/upload'"
                list-type="picture-card"
                :on-success="homesuccess"
                :limit="2"
                :file-list="homefileList"
                :before-remove="beforeRemovehome">
                <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
                <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
        </el-form-item>
        
        <el-form-item label="banner图">
            <el-upload
                :action="this.url()+'/index.php/upload/upload'"
                list-type="picture-card"
                 :on-success="handlesuccess"
                :file-list="fileList"
                :before-remove="beforeRemove">
                <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
                <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
        </el-form-item>

        <el-form-item label="详情图">
            <el-upload
                :action="this.url()+'/index.php/upload/upload'"
                list-type="picture-card"
                :on-success="infopicsuccess"
                :file-list="fileListpic"
                :before-remove="beforeRemovepic">
                <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
                <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
        </el-form-item>

        <el-form-item>
            <el-button type="primary" @click="onSubmit">立即创建</el-button>
            <el-button>取消</el-button>
        </el-form-item>
    </el-form>
     </el-main>
</template>


<script>
  export default {
    data() {
      return {
         form: {
          goods_name:'',
          cate_id:'',
          supplier_id:'',
          goods_price:'',
          market_price:'',
          goods_number:'',
          goods_sort:'',
          is_on_color: '1',
          
        },
        //规格
        formats:[
              {
                  color:'',
                  price:'',
                  smark_price:'',
                  inventory:''
              },
          ],
        //商品分类
        options: [],
        //供应商分类
        optionsupplier:[],
        //上传图片
        // fileList: [{name: 'food.jpeg', url: ''}, {name: 'food2.jpeg', url: ''}],
        fileList: [],
        fileListpic:[],
        homefileList:[],
        dialogVisible: false,
        dialogImageUrl: ''
        
      };

    },
    mounted:function(){
      this.catelist();
      this.supplierlist();
      if(this.$route.query.goods_id!=0)
      {
          //console.log(11);
        this.editgoods();
      }else{
        // console.log(22);
      }
      
    },
    
    methods: {
        //添加规格
        addformat(){
          // alert(111);
          this.formats.push({color:'',price:'',smark_price:'',inventory:''});
          
        },
        //编辑商品
      editgoods(){
          var _this=this;
          this.$axios({
              method:'get',
              url:this.url()+'/index.php/goods/onegoods',
              params:{
                  goods_id:_this.$route.query.goods_id
              }
          }).then(function(res){
              console.log(res.data.data);
              _this.form=res.data.data.goodsinfo;

              for(var i =0; i<res.data.data.goodsimg.length; i++)
              {
                res.data.data.goodsimg[i].url=_this.url()+res.data.data.goodsimg[i].url;
              }

              _this.fileList=res.data.data.goodsimg;

              for(var i =0; i<res.data.data.goodsinfopic.length; i++)
              {
                res.data.data.goodsinfopic[i].url=_this.url()+res.data.data.goodsinfopic[i].url;
              }
          
              _this.fileListpic=res.data.data.goodsinfopic;

              for(var i =0; i<res.data.data.goods_thumb.length; i++)
              {
                res.data.data.goods_thumb[i].url=_this.url()+res.data.data.goods_thumb[i].url;
              }
             
             _this.homefileList=res.data.data.goods_thumb;


              _this.formats=res.data.data.goods_formats;
              
          });
       }, 
      //提交表单
      onSubmit() {
          var _this = this;
             console.log(_this.form);
             console.log(_this.formats);
              console.log(_this.homefileList);
              console.log(_this.fileList);
          
          this.$axios({
              method: 'post',
              url:this.url()+'/index.php/goods/ceshi',
              params : {
                data:_this.form,
                goods_id:_this.$route.query.goods_id,
                fileList:_this.fileList,
                formats:_this.formats,
                homefileList:_this.homefileList,
                fileListpic:_this.fileListpic
              }
          }).then(function(res){
                _this.$router.push('/right');
                console.log(res.data);
          });
      },
      //商品分类列表
      catelist(){
          var _this = this;
        this.$axios(
        {
            method: 'get',
            url: this.url()+'/index.php/cate/cate'
        }
        ).then(function (res) {
                _this.options=res.data.data;
                //console.log(res.data.data);
            })
            .catch(function (response) {
                console.log(response);
            });
      },
       //供应商列表
      supplierlist(){
          //_this代表return
          var _this = this;
        this.$axios(
        {
            method: 'get',
            url: this.url()+'/index.php/supplier/supplier'
        }
        ).then(function (res) {
                _this.optionsupplier=res.data.data;
                //console.log(res.data.data);
            })
            .catch(function (response) {
                console.log(response);
            });
      },
      //删除banner图
      beforeRemove(file, fileList) {
        // console.log(44);
        this.$axios({
              method: 'post',
              url:this.url()+'/index.php/goods-img/imgdel',
              params : {
                img_id: file.img_id
              }
          }).then(function(res){
            // alert(res.data.data);
            // _this.$router.push('/right');
            // console.log(res.data);
          });
        //return this.$confirm(`确定移除 ${ file.name } ${ file.img_id }？`);
      },
      //提交banner图
      handlesuccess(response, file, fileList){  
          var _this = this;
            if(_this.$route.query.goods_id!=0)
            {
                 this.$axios({
                    method: 'post',
                    url:this.url()+'/index.php/goods-img/imgadd',
                    params : {
                        goods_id: _this.$route.query.goods_id,
                        img_url:response.data.url
                    }
                }).then(function(res){
                   // alert(res.data.data);
                    // _this.$router.push('/right');
                    // console.log(res.data);
                });
            }
         file=response.data;
         //console.log(file);
         this.fileList.push(file);
         console.log(this.fileList);
         
      },
      //添加商品详情
      infopicsuccess(response, file, fileList){  
          var _this = this;
            if(_this.$route.query.goods_id!=0)
            {
                 this.$axios({
                    method: 'post',
                    url:this.url()+'/index.php/goods-info-pic/imgadd',
                    params : {
                        goods_id: _this.$route.query.goods_id,
                        img_url:response.data.url
                    }
                }).then(function(res){
                   
                });
            }
            file=response.data;
            //console.log(file);
            this.fileListpic.push(file);
      },
      //删除商品详情图
      beforeRemovepic(file, fileList) {
        this.$axios({
              method: 'post',
              url:this.url()+'/index.php/goods-info-pic/imgdel',
              params : {
                pic_id: file.pic_id
              }
          }).then(function(res){
           
          });
      },
      
      //修改首页主图
      homesuccess(response, file, fileList){  
          var _this = this;
            if(_this.$route.query.goods_id!=0)
            {
                 this.$axios({
                    method: 'post',
                    url:this.url()+'/index.php/goods/imgadd',
                    params : {
                        goods_id: _this.$route.query.goods_id,
                        goods_thumb:response.data.url
                    }
                }).then(function(res){
                   
                });
            }
            file=response.data;
         console.log(file);
         _this.homefileList.push(file);
         console.log(_this.homefileList);
      },
      //删除首页主图
      beforeRemovehome(file, fileList) {
        this.$axios({
              method: 'post',
              url:this.url()+'/index.php/goods/imgdel',
              params : {
                goods_id: this.$route.query.goods_id,
              }
          }).then(function(res){
           
          });
      }


    }
  };
</script>






